import React from "react"
import PropTypes from "prop-types"
import { graphql, useStaticQuery } from "gatsby"
import "./base.css"

const Layout = ({ location, children }) => {
  const layoutData = useStaticQuery(graphql`
    query LayoutQuery {
      site {
        siteMetadata {
          title
          social {
            github
            linkedin
          }
        }
      }
    }
  `)
  // URL for GitHub
  const githubLink = `https://github.com/${layoutData.site.siteMetadata.social.github}`
  // URL for LinkedIn
  const linkedinLink = `https://linkedin.com/in/${layoutData.site.siteMetadata.social.linkedin}`
  return (
    <div className={location === "home" ? "home-container" : "child-container"}>
      <main>{children}</main>
      <footer className="footer-container">
        <p className="footer-info">
          <a href={githubLink} rel="noopener noreferrer">
            <i className="nes-icon github is-medium"></i>
          </a>
          <a href={linkedinLink} rel="noopener noreferrer">
            <i className="nes-icon linkedin is-medium"></i>
          </a>
          <a href={`mailto:andrewstolzle@gmail.com`}>
            <i className="nes-icon gmail is-medium"></i>
          </a>
          <span className="mrg-right-sml">© 2020 Andrew Stolzle with</span> <i className="nes-icon is-small heart"></i>
          <span className="mrg-left-sml">
            for{" "}
            <a href={`https://nostalgic-css.github.io/NES.css/`} rel="noopener noreferrer">
              NES.CSS
            </a>
          </span>
        </p>
      </footer>
    </div>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
